<template>
    <div class="stuworklist">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item>{{items.name}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container" v-loading="loading">
            <div class="form">
                <h3>{{items.name}}</h3>
                <el-form label-width="110px">
                    <el-form-item class="work-date" size="small" label="作业截止日期：">
                        {{items.deadlineTime}}
                    </el-form-item>
                    <el-form-item label="教师提示：">
                        <div class="teacherishi">{{items.teacherReminder}}</div>
                    </el-form-item>
                    <el-form-item label="学生作业：">
                        <el-table
                            class="stu-list" 
                            size="mini"
                            height="330"
                            :data="items.workForTeacherDTOList"
                            style="width: 100%">
                            <el-table-column
                            type="index"
                              label="序号">
                            </el-table-column>
                            <el-table-column
                              label="姓名">
                              <template slot-scope="scope">
                                <span>{{ scope.row.studentName }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="入学年份">
                              <template slot-scope="scope">
                                <span>{{ scope.row.enrollmentYear }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="学号">
                              <template slot-scope="scope">
                                <span>{{ scope.row.studentNumber }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="院系">
                              <template slot-scope="scope">
                                <span>{{ scope.row.academyName }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="班级">
                              <template slot-scope="scope">
                                <span>{{ scope.row.className }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="作业项目">
                              <template slot-scope="scope">
                                <span>{{scope.row.workProjectName}}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="作业状态">
                              <template slot-scope="scope">
                                <span>{{scope.row.statusDescribe}}</span>
                              </template>
                            </el-table-column>
                            <el-table-column label="操作" width="80px">
                              <template slot-scope="scope">
                                <el-button type="text" @click="viewwork(scope.row.id)">{{scope.row.status==0||scope.row.status==3?'查看作业':'批阅作业'}}</el-button>
                              </template>
                            </el-table-column>
                          </el-table>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import {fetchGetteachingbyId} from '../../axios/requests'
export default {
    name: 'course',
    data() {
        return {
            id:'',
            loading:false,
            items:''
        };
    },
    mounted(){
      let {pid} = this.$route.params;
      this.id = pid;
      this.GetteachingbyId()
    },
    methods: {
      viewwork(id){
        this.$openBlank('Sworkdetail',{id});
      },
      async GetteachingbyId() {//得到教学详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetteachingbyId(id);
          if (response.status.code ==0) {
            this.items = response.result;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
    }
};
</script>

<style lang='scss'>

</style>

<style lang='scss' scoped>
    .stuworklist{
      .crumbs{
          padding-left:20px;
        }
        .form{
          color:#444;
          .work-date{
            margin-bottom:0;
          }
          h3{
            font-size:16px;
            margin-bottom:15px;
          }
          .teacherishi{
            line-height:22px;
            padding-top:9px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
    }
</style>


